import Home from "../views/home/Home"
import Header from "../views/header/Header";
import TaskLog from "../components/Projects/PastProjects/TaskLog";
import UserAuth from "../components/Projects/PastProjects/UserAuth";
import PriceForecasting from "../components/Projects/PastProjects/PriceForecasting";
import Website from "../components/Projects/Current/Website";
import NewEarth from "../components/Books/NewEarth";

import { BrowserRouter as Link, Switch, Route } from "react-router-dom";

const Routes = () => {
    return (
        <>
            <Route path="/home" component={Header} />
            <Route path="/home" component={Home} />
            <Switch>
                <Route path="/tasklogger" component={TaskLog} />
                <Route path="/userauth" component={UserAuth} />
                <Route path="/priceforecasting" component={PriceForecasting} />
                <Route path="/thiswebsite" component={Website} />
                <Route path="/new-earth" component={NewEarth} />

            </Switch>


        </>
    )
}

export default Routes


